.card-collapse {
    .card {
        margin-bottom: 0;
        background: transparent;
        box-shadow: none;

        .card-header {
            position: relative;
            padding: 1rem 0.75rem;

            a[data-bs-toggle='collapse'] {
                display: block;
                color: rgba($black, 0.6);

                i {
                    float: right;
                    position: relative;
                    color: $primary;
                    top: 1px;
                    @extend .animation-transition-general !optional;
                }
            }

            &:after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 1px;
                background: rgba($black, 0.1);
            }
        }

        .card-body {
            padding-left: 1.5rem;
            padding-right: 1.5rem;
            color: rgba($black, 0.5);
        }

        &:last-child {
            .card-header:after {
                content: none;
            }
        }
    }
}
