.card-chart {
    .card-header {
        .card-title {
            i {
                font-size: 16px;
                margin-right: 5px;
                margin-bottom: 3px;
            }
        }
        .card-category {
            margin-bottom: 5px;
        }
    }

    .card-body {
        padding-top: 0;
        padding-left: 5px;
        padding-right: 5px;
    }

    .table {
        margin-bottom: 0;

        td {
            border-top: none;
            border-bottom: 1px solid rgba($white, 0.1);
        }
    }

    .card-progress {
        margin-top: 30px;
        padding: 0 10px;
    }

    .chart-area {
        min-height: 220px;
        width: 100%;
    }
    .card-footer {
        margin-top: 15px;

        .stats {
            color: $gray-800;
        }
    }

    &.card-chart-pie {
        .chart-area {
            padding: 10px 0 25px;
            height: auto;
        }

        .card-title {
            margin-bottom: 10px;

            i {
                font-size: 1rem;
            }
        }
    }
}

@include media-breakpoint-down(sm) {
    .card.card-chart {
        .card-header .btn-group-toggle {
            @include icon-classes {
                font-size: $font-size-base;
                top: -1px;
            }
        }
    }
}
