.footer {
    padding: 17px 30px 17px 280px;

    [class*='container-'] {
        padding: 0;
    }

    .nav {
        float: left;
        margin-bottom: 0;
        padding: 0;
        list-style: none;
    }

    .nav-item {
        display: inline-block;

        &:first-child a {
            padding-left: 0;
        }
    }

    .nav-link {
        color: $dark-states;
        padding: $padding-base-vertical;
        font-size: $font-size-sm;
        text-transform: uppercase;
        text-decoration: none;

        &:hover {
            text-decoration: none;
        }
    }

    .copyright {
        font-size: $font-size-sm;
        line-height: 1.8;
        color: $dark-states;
    }

    &:after {
        display: table;
        clear: both;
        content: ' ';
    }
}

@include media-breakpoint-down(md) {
    .footer {
        padding-left: 30px;
        text-align: center;

        .copyright {
            text-align: right;
        }
    }
}

@include media-breakpoint-up(xl) {
    .footer {
        .copyright {
            float: right;
            margin-top: 5px;
        }
    }
}

@include media-breakpoint-down(sm) {
    .footer {
        text-align: center;

        nav {
            display: block;
            margin-bottom: 5px;
            float: none;
        }

        .copyright {
            text-align: center;
        }
    }
}

@include media-breakpoint-down(sm) {
    .footer {
        .copyright {
            text-align: center;
        }

        .nav {
            float: none;
        }
    }
}

@include media-breakpoint-between(md, lg) {
    .footer {
        padding-left: 30px !important;
        text-align: center;

        .copyright {
            float: right;
            margin-top: 5px;
        }
    }
}
