.fileinput {
    position: relative;
    width: 200px;

    .thumbnail {
        margin: 10px;

        &.img-circle {
            border-radius: 50%;
        }
    }
}

.flatpickr {
    .form-control {
        background-color: transparent;
        cursor: pointer;
        width: 150px;
    }
}
